<template>
  <div id="record-search">
    <header>
      <img src="../../assets/imgs/images/pre.png" @click="goback" />
      <p>读本库</p>
    </header>
    <div class="input">
      <input
        v-model="inputValue"
        type="text"
        placeholder="输入诗人、诗歌标题"
      />
      <span @click="getListData">搜索</span>
    </div>
    <div class="list" v-if="listData !== null">
      <RecordSection :listData="listData"></RecordSection>
    </div>
  </div>
</template>

<script>
import RecordSection from "@/components/RecordSection.vue";
export default {
  components: { RecordSection },
  name: "recordSearch",
  data() {
    return {
      inputValue: "",
      listData: [],
    };
  },
  methods: {
    goback() {
      this.$router.back(-1);
    },
    async getListData() {
      let data = await this.$axios({
        method: "post",
        url: "https://app.thepoemforyou.com/api/v1/getPoemListApp",
        data: {
          page: 1,
          limit: 12,
          str: this.inputValue,
          classify_id: "",
        },
      });

      this.listData = data.data.data.data;
    },
  },
};
</script>

<style lang="scss" scoped>
#record-search {
  header {
    position: sticky;
    top: 0;
    left: 0;
    padding: 10px 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    img {
      width: 10px;
    }
    p {
      width: 100%;
      font-size: 15px;
      text-align: center;
    }
  }
  .input {
    padding: 10px 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    input {
      flex: 1;
      border-radius: 15px;
      padding: 8px 12px;
      border: 0px;
      margin-right: 5px;
      background-color: rgb(223, 220, 220, 0.3);
      outline: 0px;
    }
    span {
      color: var(--theme-green);
    }
  }
}
</style>